
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 46px;
  z-index: 10;
  width: 100%;
  background: #fff;
}
.food.move-enter-active, .food.move-leave-active {
    transition: all .5s;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.food.move-enter, .food.move-leave-to {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
.food .image-header {
    position: relative;
    width: 100px;
    padding-top: 100%;
}
.food .image-header img {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
}
.food .image-header .back {
      position: absolute;
      top: 10px;
      left: 5px;
      text-align: center;
}
.food .image-header .back .icon {
        display: inline-block;
        padding: 10px;
        color: #fff;
        font-size: 16px;
        width: 16px;
        height: 16px;
        line-height: 16px;
        background: rgba(227, 217, 227, 0.3);
        border-radius: 50%;
}
.food .content {
    position: relative;
    padding: 18px;
}
.food .content .title {
      line-height: 14px;
      margin-bottom: 8px;
      font-size: 14px;
      font-weight: 700;
      color: #07111b;
}
.food .content .detail {
      margin-bottom: 18px;
      line-height: 10px;
      height: 10px;
      font-size: 0;
}
.food .content .detail .sell-count,
      .food .content .detail .rating {
        font-size: 10px;
        color: #93999f;
}
.food .content .detail .sell-count {
        margin-right: 12px;
}
.food .content .price {
      font-weight: 700;
      line-height: 24px;
      font-size: 0;
}
.food .content .price .now {
        margin-right: 8px;
        font-size: 14px;
        color: red;
}
.food .content .price .old {
        font-size: 10px;
        color: #93999f;
        text-decoration: line-through;
}
.food .content .cartcontrol-wrapper {
      position: absolute;
      right: 12px;
      bottom: 12px;
}
.food .content .buy {
      position: absolute;
      right: 18px;
      bottom: 18px;
      z-index: 10;
      height: 24px;
      line-height: 24px;
      padding: 0 12px;
      box-sizing: border-box;
      font-size: 10px;
      border-radius: 12px;
      color: #fff;
      background: #00a0dc;
}
.food .content .buy.fade-enter-active, .food .content .buy.fade-leave-active {
        transition: all .5s;
}
.food .content .buy.fade-enter, .food .content .buy.fade-leave-to {
        opacity: 0;
}
.food .info {
    padding: 18px;
}
.food .info .title {
      line-height: 14px;
      font-size: 14px;
      font-weight: 500;
      margin-bottom: 6px;
      color: #07111b;
}
.food .info .text {
      line-height: 24px;
      font-size: 12px;
      font-weight: 400;
      padding: 0 8px;
      text-align: justify;
      color: #4d555d;
}
.food .rating {
    padding-top: 18px;
}
.food .rating .title {
      line-height: 14px;
      margin-left: 18px;
      font-size: 14px;
      color: #07111b;
}
.food .rating .rating-wrapper {
      padding: 0 18px;
}
.food .rating .rating-wrapper .rating-item {
        position: relative;
        padding: 16px 0;
        position: relative;
}
.food .rating .rating-wrapper .rating-item::after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          border-top: 1px solid rgba(7, 17, 27, 0.1);
          content: '';
}
.food .rating .rating-wrapper .rating-item .user {
          position: absolute;
          right: 0;
          top: 16px;
          line-height: 12px;
          font-size: 0;
          color: #93999f;
}
.food .rating .rating-wrapper .rating-item .user .name {
            display: inline-block;
            margin-right: 6px;
            vertical-align: top;
            font-size: 10px;
}
.food .rating .rating-wrapper .rating-item .user .avatar {
            border-radius: 50%;
}
.food .rating .rating-wrapper .rating-item .time {
          margin-bottom: 6px;
          line-height: 12px;
          font-size: 10px;
          color: #93999f;
}
.food .rating .rating-wrapper .rating-item .text {
          line-height: 16px;
          font-size: 12px;
          color: #07111b;
}
.food .rating .rating-wrapper .rating-item .text .icon {
            margin-right: 4px;
            line-height: 16px;
            font-size: 12px;
}
.food .rating .rating-wrapper .rating-item .text .icon.icon-thumb_up {
              color: #00a0dc;
}
.food .rating .rating-wrapper .rating-item .text .icon.icon-thumb_down {
              color: #93999f;
}
.food .rating .rating-wrapper .no-rating {
        padding: 16px 0;
        font-size: 12px;
        color: #93999f;
}



