
@charset "UTF-8";
.shopcart {
  position: fixed;
  z-index: 50;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 46px;
  background: #ccc;
}
.shopcart .content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    color: rgba(255, 255, 255, 0.4);
    background: #141d27;
}
.shopcart .content .content-left {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      font-size: 0;
}
.shopcart .content .content-left .logo-wrapper {
        display: inline-block;
        position: relative;
        top: -10px;
        margin: 0 12px;
        padding: 6px;
        width: 56px;
        height: 56px;
        border-radius: 50%;
        background: #141d27;
        box-sizing: border-box;
        vertical-align: top;
        text-align: center;
}
.shopcart .content .content-left .logo-wrapper .logo {
          width: 100%;
          height: 100%;
          border-radius: 50%;
          background: #2b333b;
}
.shopcart .content .content-left .logo-wrapper .logo.highlight {
            background: #00a0dc;
}
.shopcart .content .content-left .logo-wrapper .logo.highlight .icon-shopping_cart {
              color: #fff;
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart {
            line-height: 44px;
            font-size: 24px;
            color: rgba(255, 255, 255, 0.4);
}
.shopcart .content .content-left .logo-wrapper .num {
          position: absolute;
          top: 0;
          right: 0;
          width: 24px;
          height: 16px;
          line-height: 16px;
          text-align: center;
          border-radius: 16px;
          font-size: 9px;
          font-weight: 700;
          color: #fff;
          background: #f01414;
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}
.shopcart .content .content-left .price {
        display: inline-block;
        vertical-align: top;
        line-height: 24px;
        margin-top: 12px;
        padding-right: 12px;
        border-right: 1px solid rgba(255, 255, 255, 0.1);
        color: rgba(255, 255, 255, 0.4);
        font-size: 18px;
        font-weight: 700;
}
.shopcart .content .content-left .price.highlight {
          color: #fff;
}
.shopcart .content .content-left .desc {
        display: inline-block;
        vertical-align: top;
        margin: 12px 0 0 12px;
        line-height: 24px;
        font-size: 12px;
}
.shopcart .content .content-right {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 105px;
              flex: 0 0 105px;
      width: 105px;
      background: #2b333b;
}
.shopcart .content .content-right .pay {
        line-height: 48px;
        text-align: center;
        font-size: 14px;
        font-weight: 700;
}
.shopcart .content .content-right .pay.not-enough {
          background: #2b333b;
}
.shopcart .content .content-right .pay.enough {
          background: #00b43c;
          color: #fff;
}
.shopcart .ball-container .ball {
    position: fixed;
    left: 32px;
    bottom: 22px;
    z-index: 200;
    transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
    /* bezier曲线，详情看"http://cubic-bezier.com/#.49,-0.29,.75,.41" */
}
.shopcart .ball-container .ball .inner {
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background: #00a0dc;
      transition: all 0.4s linear;
}
.shopcart .shopcart-list {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
}
.shopcart .shopcart-list.fold-enter-active, .shopcart .shopcart-list.fold-leave-active {
      transition: all .5s;
}
.shopcart .shopcart-list.fold-enter, .shopcart .shopcart-list.fold-leave-to {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
}
.shopcart .shopcart-list .list-header {
      height: 40px;
      line-height: 40px;
      padding: 0 18px;
      background: #f3f5f7;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.shopcart .shopcart-list .list-header .title {
        float: left;
        font-size: 14px;
        font-weight: 400;
        color: #07111b;
}
.shopcart .shopcart-list .list-header .empty {
        float: right;
        font-size: 12px;
        color: #00a0dc;
}
.shopcart .shopcart-list .list-content {
      max-height: 220px;
      overflow: hidden;
      background: #fff;
      padding: 0 18px;
}
.shopcart .shopcart-list .list-content .food {
        position: relative;
        padding: 12px 0;
        box-sizing: border-box;
        position: relative;
        height: 48px;
}
.shopcart .shopcart-list .list-content .food::after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          border-top: 1px solid rgba(7, 17, 27, 0.1);
          content: '';
}
.shopcart .shopcart-list .list-content .food .name {
          line-height: 24px;
          font-size: 14px;
          color: #07111b;
}
.shopcart .shopcart-list .list-content .food .price {
          position: absolute;
          right: 90px;
          bottom: 12px;
          line-height: 24px;
          font-weight: 14px;
          font-weight: 700;
          color: #f01414;
}
.shopcart .shopcart-list .list-content .food .cartcontrol-wrapper {
          position: absolute;
          right: 0;
          bottom: 6px;
}
.shopcart .list-mask {
    position: fixed;
    z-index: -10;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all .5s;
    background: rgba(7, 17, 27, 0.6);
    -webkit-filter: blur(10px);
            filter: blur(10px);
}
.shopcart .list-mask.fade-enter-active, .shopcart .list-mask.fade-leave-active {
      transition: all .5s;
}
.shopcart .list-mask.fade-enter, .shopcart .list-mask.fade-leave-to {
      opacity: 0;
}



